.statistics {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.title {
    text-align: center;
    font-size: 24px;
    padding: 16px;    
    color: #333;
}

.stat-list {
    display: flex;    
    justify-content: center;
    list-style: none;
    padding: 0;
    
}

.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3px 10px;
    flex-direction: column;
    border: 1px solid #ccc;
    background-color: #f0f0f0;
}

.label {
    font-size: 16px;
    color: #555;
}

.percentage {
    font-size: 20px;
    color: #007bff;
    font-weight: bold;
}